import { USA_STATES } from "../util/usaStates";

FCBH.docReady(() => {
  new ResourcesForPartners();
});

class ResourcesForPartners {
  constructor() {
    this.renderStateList();
    this.renderContactOptions();
  }

  renderStateList() {
    const stateContainer = document.querySelector("#Resources-Ministry-Partners-State");
    while (stateContainer.lastChild) {
      stateContainer.removeChild(stateContainer.lastChild);
    }
    const states = USA_STATES.map((state) => {
      const node = document.createElement("option");
      node.value = state.label;
      node.label = state.label;
      return node;
    });
    stateContainer.append(...states);
  }

  renderContactOptions() {
    const contactContainer = document.querySelector("#Resources-Ministry-Partners-Contact-Method");
    while (contactContainer.lastChild) {
      contactContainer.removeChild(contactContainer.lastChild);
    }
    const options = ["Email", "Phone"].map((option) => {
      const node = document.createElement("option");
      node.value = option;
      node.label = option;
      return node;
    });
    contactContainer.append(...options);
  }
}
